<template>
  <div>
    <h1>Todo App 1</h1>
    <input v-model="newTodo" @keyup.enter="addTodo" placeholder="Add a new todo" />
    <ul>
      <li v-for="todo in todos" :key="todo.id">
        <input type="text" v-model="todo.text" @blur="updateTodo(todo)" />
        <button @click="removeTodo(todo.id)">Remove</button>
      </li>
    </ul>
  </div>
</template>

<script>
export default {
  data() {
    return {
      newTodo: '',
      todos: []
    };
  },
  created() {
    this.loadTodos();
  },
  methods: {
    async loadTodos() {
      try {
        this.todos = await window.api.getTodos();
      } catch (error) {
        console.error('Error loading todos:', error);
      }
    },
    async addTodo() {
      if (this.newTodo.trim()) {
        const newTodoItem = {
          text: this.newTodo.trim(),
        };
        try {
          this.newTodo = '';
          await window.api.addTodo(newTodoItem);
          this.todos = await window.api.getTodos();
        } catch (error) {
          console.error('Error adding todo:', error);
        }
      }
    },
    async updateTodo(todo) {
      try {
        const todoData = Object.assign({}, todo);
        // Gọi hàm update todo từ main.js
        await window.api.updateTodo(todoData);
        this.todos = await window.api.getTodos();
      } catch (error) {
        console.error('Error updating todo:', error);
      }
    },
    async removeTodo(id) {
      try {
        await window.api.removeTodo(id);
        this.todos = await window.api.getTodos();
      } catch (error) {
        console.error('Error removing todo:', error);
      }
    },
  }
};
</script>

